<script>
import bus from "@/bus.js";
import $ from "jquery";
import lessons from "./Pages/lesson";
import UserBasicInfo from "./components/layout/UserBasicInfo.vue";
import CreateStudent from "./components/Modal/CreateStudent.vue";
import InlineActionsTable from "./Students/RegularTables/InlineActionsTable";
import LessonTypeInfo from "./components/layout/LessonTypeInfo";
export default {
  components: {
    InlineActionsTable,
    UserBasicInfo,
    CreateStudent,
    LessonTypeInfo,
  },
  data() {
    return {
      previewData: {},
      testTeacherData: {
        name: 'Wayne Wang',
        email: 'mailwpw@gmail.com',
        phone: '+1 400 820 8820'
      },
      showPreview: false,
      showCreate: false,
      showDelete: false,
      showEditStudent: false,
      editLesson: false,
      showEditLessonDetail: false,
      showAddNewLesson: false,
      lessons,
    };
  },
  methods: {
    preview(data) {
      let self = this;
      self.showPreview = true;
      self.previewData = data;
    },
    onActive() {
      let self = this;
      self.previewData.archive = false;
      bus.$emit("onActive", self.previewData);
    },
    onDeleteArchive() {
      let self = this;
      self.deleteActive = false;
      self.showDelete = true;
    },
    onDelete() {
      let self = this;
      self.deleteActive = true;
      self.showDelete = true;
    },
    onArchive() {
      let self = this;
      self.previewData.archive = true;
      bus.$emit("onArchive", self.previewData);
    },
    confirmDelete() {
      let self = this;
      bus.$emit("confirmDelete", self.previewData);
      self.showDelete = false;
      self.showPreview = false;
    },
    toggleEditLesson() {
      let self = this;
      self.editLesson = !self.editLesson;
      $("#addNewLesson").slideToggle();
    },
    deleteLesson(index) {
      this.lessons.splice(index, 1);
    },
    showLessonDetail(data) {
      let self = this;
      if (!self.editLesson) {
        self.showEditLessonDetail = true;
      }
    },
  },
};
</script>
