<template>
  <div>
    <div class="card">
      <div class="card-header border-0"></div>

      <el-table
        class="table-responsive align-items-center table-flush"
        header-row-class-name="thead-light"
        :data="users"
      >
        <el-table-column label="Name" min-width="200px" prop="name" sortable>
          <template v-slot="{ row }">
            <img
              :src="row.image"
              class="avatar rounded-circle mr-3 tk-cursor-pointer tk-layout-inline-block"
              @click.prevent="preview(row)"
            />
            <span
              class="font-weight-bold tk-font-color-default tk-cursor-pointer text-sm tk-layout-inline-block tk-text-wrap"
              style="width: calc(100% - 48px - 1rem)"
              @click.prevent="preview(row)"
            >
              <b>{{ row.name }}</b>
            </span>
          </template>
        </el-table-column>

        <el-table-column label="Status" min-width="180px" prop="lesson">
          <template v-slot="{ row }">
            <!-- archive + active -->
            <template v-if="row.active">
              <div v-if="row.archive">
                <div
                  class="tk-font-color-gray tk-font-weight-bold tk-layout-inline-block"
                >
                  Archive
                </div>
                <base-button
                  size="sm"
                  type="main"
                  class="tk-font-color-white tk-layout-inline-block tk-px-margin-left-px10"
                  >Add Lesson</base-button
                >
              </div>
              <div v-else class="tk-font-color-green tk-font-weight-bold">
                Active
              </div>
            </template>
            <!-- inactive -->
            <div v-else>
              <div
                class="tk-font-color-lightGray tk-font-weight-bold tk-layout-inline-block"
              >
                Inactive
              </div>
              <base-button
                size="sm"
                type="resend"
                class="tk-font-color-white tk-layout-inline-block tk-px-margin-left-px10"
                >Resend</base-button
              >
            </div>
          </template>
        </el-table-column>
      </el-table>

      <div
        class="card-footer py-4 d-flex justify-content-end"
        v-if="users.length > 0"
      >
        <base-pagination v-model="currentPage" :total="50"></base-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import bus from "@/bus";
import users from "../students";
import archive from "../archive";
import LessonTypeInfo from "../../components/layout/LessonTypeInfo.vue";
import { Table, TableColumn } from "element-ui";

export default {
  name: "inline-actions-table",
  components: {
    // LessonTypeInfo,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      users,
      archive,
      currentPage: 1,
      currentPage2: 1,

      deleteArchive: false,

      index: -1
    };
  },
  mounted() {
    let self = this;
    bus.$on("onActive", data => {
      self.onActive(data);
    });
    bus.$on("onArchive", data => {
      self.onArchive(data);
    });
    bus.$on("confirmDelete", data => {
      // if (data.active) {
      self.onDelete(data);
      // }else {
      // self.onDeleteArchive(data)
      // }
      self.confirmDelete();
    });
  },
  methods: {
    preview(data) {
      this.$emit("preview", data);
    },
    onDelete(row) {
      // alert(`You want to delete ${row.name}`);
      let self = this;
      self.users.some((item, i) => {
        if (item.id === row.id) {
          self.index = i;
        }
      });
    },
    onDeleteArchive(row) {
      // alert(`You want to delete ${row.name}`);
      let self = this;
      self.archive.some((item, i) => {
        if (item.id === row.id) {
          self.index = i;
          self.deleteArchive = true;
        }
      });
    },
    confirmDelete() {
      let self = this;
      // if (self.deleteArchive) {
      //   self.archive.splice(self.index, 1);
      //   self.deleteArchive = false;
      // } else {
      //   self.users.splice(self.index, 1);
      // }
      self.users.splice(self.index, 1);
      self.index = -1;
    },
    onArchive(row) {
      // alert(`You want to archive ${row.name}`);
      let self = this;
      self.users.some((item, i) => {
        if (item.id === row.id) {
          // self.archive.push(row);
          // self.users.splice(i, 1);
          item.archive = true
        }
      });
    },
    onActive(row) {
      // alert(`You want to archive ${row.name}`);
      let self = this;
      self.archive.some((item, i) => {
        if (item.id === row.id) {
          // self.users.push(row);
          // self.archive.splice(i, 1);
          item.archive = false
          console.log(item);
        }
      });
    }
  }
};
</script>
